import React from 'react';
import './Story.scss'

function Story() {
  return(
    <div id='story' className='outer-section'>
        <div  className='mt-2 mb-2 section text-center'>
            <span className='font-l text-white extra-bold'>
              PHASE ONE - EVOLUTION
            </span>
      
            <div style={{margin:'auto'}} className='inner font-xs text-light-gray text-normal'>
              <br/>
              {/* <br/> */}
              Phase One 'Evolution' is our rollout NFT collection that is artworks of specially formulated designs of half-man, half-ape.
              <div className='mt-point5' />
              {/* <br/> */}
              Frustrated by the apparent decline in the evolution process among humans, a group of scientists and evolutionary biologists prescribed to the ways of Directed Evolution.
              <div className='mt-point5' />
              {/* <br/> */}
              The natural progression of technological advancement would be Technological Symbiosis through the creation of Cyborgs. However, with the lack of security and the ease with which some are able to hack into systems, this idea was discarded.
              <div className='mt-point5' />
              {/* <br/> */}
              The scientists devised a way to restart the evolutionary process, to create the next stage in evolution and combine the physical abilities of animals with the intelligence and learning capacity of humans.
              <div className='mt-point5' />
              {/* <br/> */}
              Genetic splicing would be too costly and drawn out so the scientists proposed a far more drastic approach. To physically combine a human and an ape through vivisection and to stimulate the growth of animal cells through stem cells and hormones from the human pituitary gland.
              <div className='mt-point5' />
              {/* <br/> */}
              Provided with financial aid from an unnamed source and a location for these experiments to take place, the scientists just needed some test subjects.
              5000 volunteers signed up for a 12 month sociological and behavioral study on their time while living with apes.
              <div className='mt-point5' />
              {/* <br/> */}
              Over the next 20 weeks, these volunteers would start arriving at this island. They were not aware of the plans these scientists had in store for them, unbound by the laws of any nations, these are the results of those experiments

            </div>
        </div>
    </div>
  )
}

export default Story;
