import React, { useState } from 'react'
import './Header.scss'
import logoImg from '../../assets/images/Logo.png'
import twitterLogo from '../../assets/images/twitter-logo.png'
import discordLogo from '../../assets/images/discord-logo.png'

import closeIcn from '../../assets/images/close.png'
import menuIcn from '../../assets/images/menu.png'


function Header() {


    const[mobileNavOpen,setMobilenavOpen]=useState(false)
    const navigate=(link)=>{
        window.open(link)

    }
    const navigateTo=(location)=>{

        var height = document.getElementsByClassName("Header")[0].offsetHeight + 15;
        var container = document.getElementsByClassName("App")[0];
        var offsetTop;

        if(window.innerWidth<750){
            openNavbar()
        }

        switch (location) {
            case "STORY":
              offsetTop =document.getElementById("story").offsetTop - height;
              container.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
              break;
            case "MINT":
              offsetTop =document.getElementsByClassName("MintSection")[0].offsetTop - height;
              container.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
              break;
            case "ABOUT":
              offsetTop =document.getElementsByClassName("About")[0].offsetTop- height;
              container.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
              break;
            case "ROADMAP":
              offsetTop =document.getElementById("roadmap").offsetTop - height;
              container.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
              break;
            case "TEAM":
              offsetTop =document.getElementsByClassName("team-section")[0].offsetTop - height;
              container.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
              break;
            default:
              break;
        }
    
    }
    

    const openNavbar=()=>{
        const nav=document.getElementsByClassName('Header-mobile-nav')[0];

        if(!mobileNavOpen){
            nav.style.display='flex';
            setTimeout(()=>{
                nav.style.height='100vh';
                setMobilenavOpen(true)
            },[100])
        }
        else{
            nav.style.height='0vh';
            setTimeout(()=>{
                nav.style.display='none';
                setMobilenavOpen(false)
            },[500])
        }
        
    }

    return (
        <div className='Header'>
            <div className='Header-left'>
                <img className='Header-logo'  src={logoImg} alt='logo'/>
                <div className='Header-left-nav'>
                    <span onClick={()=>navigateTo('STORY')} className='font-s text-white bold'>
                        STORY
                    </span>
                    <span onClick={()=>navigateTo('MINT')}  className='font-s text-white bold'>
                        MINT
                    </span>
                    <span onClick={()=>navigateTo('ABOUT')}  className='font-s text-white bold'>
                        ABOUT
                    </span>
                    <span onClick={()=>navigateTo('ROADMAP')}  className='font-s text-white bold'>
                        ROADMAP
                    </span>
                    <span onClick={()=>navigateTo('TEAM')}  className='font-s text-white bold'>
                        TEAM
                    </span>
                </div>
                
            </div>
            <div className='Header-mobile-nav'>
                <div className='Header-mobile-nav-inner'>
                    <img   src={logoImg} alt='logo'/>
                    <span onClick={()=>navigateTo('STORY')} className='font-m text-white bold'>
                        STORY
                    </span>
                    <span onClick={()=>navigateTo('MINT')}  className='font-m text-white bold'>
                        MINT
                    </span>
                    <span onClick={()=>navigateTo('ABOUT')}  className='font-m text-white bold'>
                        ABOUT
                    </span>
                    <span onClick={()=>navigateTo('ROADMAP')}  className='font-m text-white bold'>
                        ROADMAP
                    </span>
                    <span onClick={()=>navigateTo('TEAM')}  className='font-m text-white bold'>
                        TEAM
                    </span>
                  
                </div>
               
            </div>
            <div className='Header-right'>
                <button onClick={()=>navigate('https://twitter.com/HBHybridClub')} className='social-btn'>
                    <img src={twitterLogo} alt='logo'/>
                </button>
               
                <button onClick={()=>navigate('https://discord.io/HBHC')} className='social-btn'>
                    <img src={discordLogo} alt='logo'/>
                </button>
               
            </div>
            <button onClick={openNavbar} className='header-menu-btn' >
                {mobileNavOpen?
                
                <img src={closeIcn} alt='logo'/>:
                <img src={menuIcn} alt='logo'/>}
            </button>
        </div>
    )
}

export default Header
