import React, { useEffect, useState } from 'react'
import './Carousel.scss'
import img1 from '../../assets/images/carousel/1.png';
import img2 from '../../assets/images/carousel/2.png';
import img3 from '../../assets/images/carousel/3.png';
import img4 from '../../assets/images/carousel/4.png';
import img5 from'../../assets/images/carousel/5.png';
import img6 from'../../assets/images/carousel/6.png';
import img7 from'../../assets/images/carousel/7.png';
import img8 from'../../assets/images/carousel/8.png';
import img9 from'../../assets/images/carousel/9.png';
import img10 from'../../assets/images/carousel/10.png';
import img11 from'../../assets/images/carousel/11.png';
import img12 from'../../assets/images/carousel/12.png';


const imgNum=12

function Carousel() {
    const[animation,setAnimation]=useState({})
    const[opacity,setOpacity]=useState({})
    const[index,setIndex]=useState(0)
    
    


    useEffect(()=>{
        let classes={};
     
        let imgWidth=document.getElementsByClassName('Carousel-BodyImage')[0].offsetWidth;
        let initialPos=(imgWidth/2)+5;
        let pos=initialPos;

        window.addEventListener('resize',()=>{
           
            imgWidth=document.getElementsByClassName('Carousel-BodyImage')[0].offsetWidth;
            initialPos=(imgWidth/2)+5;
            pos=initialPos-5
     
        })
     
        classes={left:`-${pos}px` };
    
        setAnimation(classes)
  
        pos+=imgWidth

        setInterval(()=>{
            
            classes={transition: "left 500ms ease-in-out 0s" ,left:`-${pos}px`};
            setAnimation(classes)
            
            // // if(Math.floor(p)===(Math.floor(iP+(imgPercent*6)))){
            //     console.log(pos,((imgWidth)*imgNum)+(5*(imgNum-1))+initialPos)
            if(pos===(((imgWidth)*imgNum)+(5*(imgNum-1))+initialPos)){
                
         
                pos=initialPos
                setTimeout(()=>{

                    
                    classes={left:`-${pos}px` };
                    setAnimation(classes)
                
                   
                    pos+=imgWidth
                },[500])
                    
            }
            else{
               
                
                pos+=imgWidth+5
            }
            
        }, 1500);
    
        
    },[])
 
    // useEffect(()=>{
    //     switch(index){
    //         case 0:
    //             setOpacity({img5:{opacity:0.4},img2:{opacity:0.4}})
    //             break;
    //         case 1:
    //             setOpacity({img1:{opacity:0.4},img3:{opacity:0.4}})
    //             break;
    //         case Math.floor(iP+(imgPercent*2)):
    //             setOpacity({img2:{opacity:0.4},img4:{opacity:0.4}})
    //             break;
    //         case Math.floor(iP+(imgPercent*3)):
    //             setOpacity({img3:{opacity:0.4},img5:{opacity:0.4}})
    //             break;
    //         case Math.floor(iP+(imgPercent*4)):
    //             setOpacity({img4:{opacity:0.4},img6:{opacity:0.4}})
    //             break;
    //         case Math.floor(iP+(imgPercent*5)):
    //             setOpacity({img5:{opacity:0.4},img1:{opacity:0.4}})
    //             break;
    //         case Math.floor(iP+(imgPercent*6)):
    //             setOpacity({img6:{opacity:0.4},img2:{opacity:0.4}})
    //             break;
    //     }
    // },[index])
    
    return (
        <div className='Carousel'>
            
            <div className='Carousel-Header'>

                <span className='Carousel-Header-title bold'>MEET SOME HALFBREEDS</span>
                <div className='Carousel-Divider'></div>
            </div>
            <div  className='Carousel-Body' id='Carousel-Body'>
                <div className='Carousel-InnerBody' style={animation}  id='Carousel-InnerBody'>
                    <img alt="" src={img12} className="Carousel-BodyImage"></img>
                    <img alt="" src={img1} className="Carousel-BodyImage"></img>
                    <img alt="" src={img2} className="Carousel-BodyImage"></img>
                    <img  alt="" src={img3} className="Carousel-BodyImage"></img>
                    <img  alt="" src={img4} className="Carousel-BodyImage"></img>
                    <img  alt="" src={img5} className="Carousel-BodyImage"></img>
                    <img  alt="" src={img6} className="Carousel-BodyImage"></img>
                    <img  alt="" src={img7} className="Carousel-BodyImage"></img>
                    <img  alt="" src={img8} className="Carousel-BodyImage"></img>
                    <img  alt="" src={img9} className="Carousel-BodyImage"></img>
                    <img  alt="" src={img10} className="Carousel-BodyImage"></img>
                    <img  alt="" src={img11} className="Carousel-BodyImage"></img>
                    <img  alt="" src={img12} className="Carousel-BodyImage"></img>


                    <img  alt="" src={img1} className="Carousel-BodyImage"></img>
                    <img  alt="" src={img2} className="Carousel-BodyImage"></img>
                    <img  alt="" src={img3} className="Carousel-BodyImage"></img>
                </div>
            </div>
          
        </div>
    )
}

export default Carousel




