import React from 'react'

import './Team.scss'



import teamOne from '../../assets/images/Team_1.png'
import teamTwo from '../../assets/images/Team_2.png'
import teamThree from '../../assets/images/Team_7.png'

import teamFour from '../../assets/images/Team_5.png'
import teamFive from '../../assets/images/Team_4.png'





function Team() {
    return (
        <div className='team-section'>
            <span  className='font-l text-white extra-bold'>
                TEAM
            </span>
        
            <div className='mt-2 team-section-top'>
                {/* <div  className='team-progress'></div> */}
                
                <div style={{transform:'rotate(90deg)'}} className='team-chain-top'>
                    
                </div>
                <div className='team-wrapper'>
                    <img src={teamOne}/>
                    <span className='text-white font-s text-white bold mt-point5'>
                        Pete @HBHybridClub
                    </span>
                    <span className='text-light-gray font-xs text-white'>
                        Chief Executive Officer
                    </span>
                </div>
                
            </div>
            
            <div className='team-section-bottom'>
                <div className='team-section-bottom-left'>

                    <div className='team-wrapper' id='wrapper-bottom'>
                        <img src={teamTwo}/>
                    
                        <span className='text-white font-s text-white bold mt-point5'>
                            AJ @HBHybridClub
                        </span>
                        <span className='text-light-gray font-xs text-white'>
                        Chief Technical Advisor
                        </span>


                    </div>
                    <div className='team-chain-left team-chain'></div>
                    <div className='team-wrapper'  id='wrapper-bottom'>
                        <img src={teamFour}/>
                        <span></span>
                        <span className='text-white font-s text-white bold mt-point5'>
                            Sahil @weebstrator
                        </span>
                        <span className='text-light-gray font-xs text-white'>
                            Graphic Designer
                        </span>
                    </div>
                </div>
                <div className='team-section-bottom-right'>
                    

                    <div className='team-wrapper'  id='wrapper-bottom'>
                        <img src={teamThree}/>
                        <span className='text-white font-s text-white bold mt-point5'>
                            April @Aprilgallery101
                        </span>
                        <span className='text-light-gray font-xs text-white'>
                            Administration Manager
                        </span>
                    </div>

                    <div className='team-chain-right team-chain'></div>


                    <div className='team-wrapper'  id='wrapper-bottom'>
                        <img src={teamFive}/>
                        
                        <span className='text-white font-s text-white bold mt-point5'>
                            Baraka @BarakaCapShalom
                        </span>
                        <span className='text-light-gray font-xs text-white'>
                            Web3 Developer
                        </span>

                    </div>
                </div>
            </div>
          
        </div>
    )
}

export default Team
