import React from 'react'
import './About.scss'
import apeImg from '../../assets/images/example.png'

function About() {
    return (
        <div className='About mt-8'>
            <div className='About-left' >
                <span className='font-l text-white extra-bold'>
                    ABOUT
                </span>
                <div className='font-xs text-light-gray text-normal mt-1'>
                    <br/>
                    Half Breeds Hybrid Club (HBHC)- An exclusive NFT investors club with utility and focus. HBHC investors will enjoy a wide range of opportunity and utility through a three phase rollout format.
                    <div className='mt-point5' />
                    Phase One 'Evolution'-our first 5000 human's have arrived and have had a vivisection, these will be your limited utility rewards token NFT's. Phase One NFT's will have future utilities as we evolve.
                    <div className='mt-point5' />
                    Phase Two 'Rebirth' -our second set of volunteers have now arrived and a vivisection has taken place, lets welcome the women and children to the island as they are now combined with a variety of various animal traits and characteristics.
                    <div className='mt-point5' />
                    Phase Three 'Fusion 3D' -the metaverse has sprung to life, a glitch in software turned our brave 'HBHC' volunteers into a 3D version, Now allowing them to transition into the new VR world and the Metaverse that awaits.
                    <div className='mt-point5' />
                    Utility of HBHC Investors- After Phase One sellout, We will allocate 85% of all our secondary royalties to a established Community Vault to be voted on by community owners for it's distribution. Weekly rewards could include, but not limited to: Blue Chip NFT purchases, Mint Passes, $HBHC Token Drops, WL for projects, and Free NFTS.
                    <div className='mt-point5' />
                    Our Goal is to build a strong community of long term holders focused on making this a Blue Chip NFT project and business. APE-into our discord, become a family member of a utility driven business and NFT project that will have long term and short term benefits, we love flippers!!!!!!!!!
                </div>
            </div>
          
            <div className='about-img'>
                <img src={apeImg} />
            </div>
           
           
        </div>
    )
}

export default About
